@import '../resources/Variables.scss';

.boton-little {
	font-size: 16px;
	font-family: Karla;
	width: 100%;
	height: 46px;
	background-color: $colorBase;
	border-radius: 0.2em;
	margin: 1vh 0vh;
	padding: 6px 2px;
	color: white;
}

.icon-little {
	width: 25px;
	margin-right: 6px;
}

.counters {
	font-size: 16px;
	font-family: consolas;
	padding: 5px 8px;
	background-color: $colorHover;
	border-radius: 0.2em;
}

.zfill {
	color: $colorDark;
	font-weight: bold;
}
